.top-nav>.container>.brand {
 flex:1 1 auto
}
.top-nav>.container>.actions {
 flex:0 0 auto;
 display:flex
}
.top-nav>.container>.brand {
 font-size:11px;
 display:inline-block;
 font-weight:700;
 text-transform:uppercase;
 letter-spacing:.05em;
 text-decoration:none
}
@media(min-width:480px) and (max-width:768px) {
 .top-nav>.container>.brand {
  font-size:calc( 11px + 1 * ( ( 100vw - 480px)/288 ) )
 }
}
@media(min-width:768px) and (max-width:1280px) {
 .top-nav>.container>.brand {
  font-size:calc( 12px + 0 * ( ( 100vw - 768px)/512 ) )
 }
}
@media(min-width:1280px) {
 .top-nav>.container>.brand {
  font-size:12px
 }
}
.top-nav>.container>.brand,
.top-nav>.container>.brand:visited {
 color:#345
}
.top-nav>.container>.brand:hover {
 color:#745fb5
}
@media(max-width:480px) {
 .top-nav>.container>.brand {
  display:none
 }
 .top-nav>.container>.actions {
  margin-left:auto
 }
}
@media(min-width:481px) {
 .top-nav>.container>.actions {
  position:absolute;
  right:16px;
  top:16px
 }
}
@media(min-width:481px) and (max-width:480px) {
 .top-nav>.container>.actions {
  right:16px
 }
}
@media(min-width:481px) {
 .top-nav>.container>.left {
  position:absolute;
  left:16px;
  top:16px
 }
}
@media(min-width:481px) and (max-width:480px) {
 .top-nav>.container>.left {
  left:16px
 }
}
@media(min-width:1232px) {
 .top-nav>.container>.left>.home {
  position:relative;
  left:-16px
 }
}
.top-sheet.top-sheet {
 padding:4px;
 text-decoration:none
}
.top-sheet.top-sheet>.info {
 display:block;
 background:#745fb5;
 color:#fff;
 text-align:left;
 padding:16px;
 width:auto;
 border-radius:3px;
 box-shadow:0 1px 1px rgba(102,119,136,.55);
 text-shadow:0 1px 1px rgba(51,68,85,.3);
 white-space:nowrap
}
.top-sheet.top-sheet:hover>.info,
.top-sheet.top-sheet:focus>.info {
 box-shadow:0 6px 8px rgba(102,119,136,.03),0 1px 2px rgba(102,119,136,.3)
}
.top-sheet.top-sheet:nth-of-type(3n+1)>.info {
 background:linear-gradient(15deg,#745fb5,#9a6dbb)
}
.top-sheet.top-sheet:nth-of-type(3n+1):hover>.info,
.top-sheet.top-sheet:nth-of-type(3n+1):focus>.info {
 background:#5f4aa1
}
.top-sheet.top-sheet:nth-of-type(3n+2)>.info {
 background:linear-gradient(15deg,#678,#6f8793)
}
.top-sheet.top-sheet:nth-of-type(3n+2):hover>.info,
.top-sheet.top-sheet:nth-of-type(3n+2):focus>.info {
 background:#556371
}
.top-sheet.top-sheet:nth-of-type(3n+3)>.info {
 background:linear-gradient(15deg,#14af83,#15b89a)
}
.top-sheet.top-sheet:nth-of-type(3n+3):hover>.info,
.top-sheet.top-sheet:nth-of-type(3n+3):focus>.info {
 background:#108b68
}
.top-sheet.top-sheet>.info>.title {
 display:none
}
